
var api = require('../api');
var view = require('../view');
var users_item = require('../vm/users_item');

var template = "<h2>Users</h2>\n<!-- ko if: !permission() -->\n    <div class=\"alert alert-warning\" role=\"alert\">\n        You do not have permissions to manage users.\n    </div>\n<!-- /ko -->\n<!-- ko if: permission() -->\n    <ul data-bind=\"foreach: users\" class=\"list-group\">\n        <li class=\"list-group-item post-item\">\n            <h4 data-bind=\"click: expand, text: fullname\" class=\"list-group-item-heading\"></h4>\n            <div class=\"list-group-item-text\">\n                Type: <span data-bind=\"text: type\"></span>,\n                username: <span data-bind=\"text: username\"></span>\n                <div data-bind=\"visible: expanded\" class=\"post-controls\">\n                    <a data-bind=\"attr: { href: '#user/' + $id }, css: { disabled: !editable }\"\n                        class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-edit\"></span> Edit\n                    </a>\n                    <a data-bind=\"click: remove, css: { disabled: !editable }\"\n                        class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-remove\"></span> Remove\n                    </a>\n                </div>\n            </div>\n        </li>\n    </ul>\n    <a class=\"btn btn-default\" href=\"#user/new\">\n        <span class=\"glyphicon glyphicon-plus\"></span> Add\n    </a>\n<!-- /ko -->\n";

// Page for displaying the users list.

exports.create = function() {

    var model = {

        users: ko.observableArray([]),

        permission: ko.observable(false)
    };

    return api.userInfo().then(function(info) {

        if (info.type !== 'admin') {

            view.show(template, model);

        } else {

            return api.users().then(function(users) {

                model.users(users.map(function(data) {

                    var user = users_item.create(data);

                    user.editable = info.type === 'admin' || user.$id === info.$id;

                    return user;

                }));

                model.permission(true);

                view.show(template, model);
            });
        }
    });
};
