
var api = require('../api');
var post = require('../vm/post');
var view = require('../view');
var resolveObject = require('../resolve_object');

var template = "<!-- ko with: post -->\n    <h2 data-bind=\"text: title\"></h2>\n    <form data-bind=\"submit: submit\" autocomplete=\"off\" id=\"post\">\n        <div class=\"form-group\">\n            <label for=\"post-title\">Title</label>\n            <input data-bind=\"textInput: title\"\n                type=\"text\" class=\"form-control\" id=\"post-title\"\n                placeholder=\"Title\">\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-slug\">Slug</label>\n            <input data-bind=\"textInput: slug\"\n                type=\"text\" class=\"form-control\" id=\"post-slug\"\n                placeholder=\"Slug\">\n            <p class=\"help-block\">Slug should not be modified after publishing as it will break links.</p>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-description\">Description</label>\n            <textarea\n                data-bind=\"textInput: description\"\n                class=\"form-control\" rows=\"3\" id=\"post-description\" placeholder=\"Description\"></textarea>\n            <p class=\"help-block\">Description is used by search engines to show the page snippet.</p>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-content\">Content</label>\n            <textarea\n                data-bind=\"textInput: content\"\n                class=\"form-control content\" rows=\"20\" id=\"post-content\"\n                placeholder=\"Content\"></textarea>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-type\">Type</label>\n            <select data-bind=\"value: type, options: types,\n                optionsText: 'label', optionsValue: 'name',\n                attr: { disabled: !can_change_type }\"\n                class=\"form-control\" id=\"post-type\"></select>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-content-type\">Content type</label>\n            <select data-bind=\"value: content_type\"\n                class=\"form-control\" id=\"post-content-type\">\n                <option value=\"markdown\">Markdown</option>\n                <option value=\"raw\">Raw</option>\n            </select>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-author\">Author</label>\n            <select data-bind=\"value: author, options: authors,\n                optionsText: 'fullname', optionsValue: '$id',\n                attr: { disabled: !can_change_author }\"\n                class=\"form-control\" id=\"post-author\"></select>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-tags\">Tags</label>\n            <input data-bind=\"textInput: tags\"\n                type=\"text\" class=\"form-control\" id=\"post-tags\"\n                placeholder=\"Tags\">\n            <p class=\"help-block\">Separate multiple tags with commas.</p>\n        </div>\n        <div class=\"checkbox\">\n            <label><input data-bind=\"checked: published,\n                attr: { disabled: !can_publish }\"\n                type=\"checkbox\" id=\"post-published\"> Published</label>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-date\">Publish date</label>\n            <input data-bind=\"textInput: date\"\n                type=\"text\" class=\"form-control\" id=\"post-date\"\n                placeholder=\"YYYY-MM-DD\">\n        </div>\n        <div class=\"form-group\">\n            <label for=\"post-update\">Update date</label>\n            <input data-bind=\"textInput: update\"\n                type=\"text\" class=\"form-control\" id=\"post-update\"\n                placeholder=\"YYYY-MM-DD\">\n        </div>\n            <div class=\"checkbox\">\n                <label><input data-bind=\"checked: commenting\"\n                    type=\"checkbox\"> Commenting</label>\n            </div>\n        <div class=\"form-group\">\n            <label for=\"post-language\">Language</label>\n            <select data-bind=\"value: language, options: languages,\n                optionsText: 'name', optionsValue: 'code'\"\n                class=\"form-control\" id=\"post-language\"></select>\n        </div>\n        <!-- ko if: can_manage_files -->\n            <!-- ko component: { name: 'files',\n                params: { id: $id, slug: slug, slug_changed: slug_changed } } --><!-- /ko -->\n        <!-- /ko -->\n        <!-- ko if: !can_manage_files -->\n            <div class=\"alert alert-warning\" role=\"alert\">\n                To upload files you need file management permissions.\n            </div>\n        <!-- /ko -->\n        <button data-bind=\"click: save\" class=\"btn btn-default\">\n            <span class=\"glyphicon glyphicon-ok\"></span> Save/leave\n        </button>\n        <button type=\"submit\" class=\"btn btn-default\">\n            <span class=\"glyphicon glyphicon-ok\"></span> Save/continue\n        </button>\n        <a data-bind=\"attr: { href: '#entries/' + type() }\" class=\"btn btn-default\" href=\"#\">\n            <span class=\"glyphicon glyphicon-arrow-left\"></span> Entries\n        </a>\n    </form>\n<!-- /ko -->\n";

// The post edit page.

exports.create = function(type, id) {

    var model = {

        post: ko.observable()
    };

    return postData(id).then(function(data) {

        // data.post will be undefined when id is not set.

        model.post(post.create(data.userInfo, type, data.types, data.users, data.post));

        view.show(template, model);

        if (id) {

            // Existing post.
            // Autoset initial textarea height.

            var editor = document.getElementById('post-content');

            editor.style.height = (editor.scrollHeight + 10) + 'px';

        } else {

            // Set focus to title.

            var title = document.querySelector('#post-title');

            title.focus();

            if (typeof title.setSelectionRange === 'function') {

                title.setSelectionRange(0, title.value.length);
            }
        }
    });
};

// Retrieves data relevant to
// the post. When id is not set
// then retrieves data needed for the
// new post.

function postData(id) {

    return api.userInfo().then(function(userInfo) {

        var requests = {

            types: api.types(),

            users: authors(userInfo),

            userInfo: Promise.resolve(userInfo)
        };

        if (id) {

            requests.post = api.post(id);
        }

        return resolveObject(requests);
    });
}

// Retrieves selectable authors.
// userInfo - the current user info data.

function authors(userInfo) {

    if (userInfo.type === 'admin') {

        return api.users();

    } else {

        // Non-admin can only select
        // itself as the author.

        return Promise.resolve([{

            $id: userInfo.$id,

            fullname: userInfo.fullname

        }]);
    }
}
